import React, { useState } from "react"
import { Link } from "react-router-dom"

import { Container, Collapse, Navbar, NavbarToggler, NavbarBrand, Nav, NavItem } from "reactstrap"

function Header() {
  const [isOpen, setIsOpen] = useState(false)

  const toggle = () => setIsOpen(!isOpen)
  return (
    <>
      <Navbar color="light" light expand="md" className="py-0">
        <Container>
          <NavbarBrand href="/" style={{ color: "#fff", fontSize: "2rem" }}>
            NareshOnLine
          </NavbarBrand>
          <NavbarToggler onClick={toggle} />
          <Collapse isOpen={isOpen} navbar>
            <Nav className="ml-auto" navbar>
              <NavItem>
                <Link to="/components/" className="nav-link">
                  Home
                </Link>
              </NavItem>
              <NavItem>
                <Link to="/components/" className="nav-link">
                  Blog
                </Link>
              </NavItem>
              <NavItem>
                <Link to="/components/" className="nav-link">
                  Contacts Us
                </Link>
              </NavItem>
            </Nav>
          </Collapse>
        </Container>
      </Navbar>
    </>
  )
}

export default Header
